export default function Works() {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-8 px-6 py-10">
      {projects.map((project, index) => (
        <Link
          key={index}
          href={project.link}
          className="card-child flex flex-col items-center"
        >
          <div className="overflow-hidden rounded-xl">
            <Image
              priority
              className="rounded-xl transition-transform duration-300 ease-in-out transform hover:scale-105"
              src={project.image}
              alt={project.title}
            />
          </div>
          <div className="pt-6 text-white text-left">
            <p className="text-xl md:text-2xl text-gray-500 font-bold py-2">
              {project.role}
            </p>
            <h1 className="text-xl md:text-2xl">
              <span className="text-4xl md:text-5xl font-bold">
                {project.title}
              </span>{" "}
              {project.description}
            </h1>
          </div>
        </Link>
      ))}
    </div>
  );
}
